import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
import { requiredRule } from 'utils/validateRules';
import {
  getRatingCertificateById,
  createRatingCertificate,
  updateRatingCertificate,
} from 'services/ratingCertificate';

@Form.create({ name: 'formTeacher' })
class FormRatingGraduate extends Component {
  state = {
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    const { id } = match.params;
    if (match.params.id) {
      this.getRatingCertificateById(id);
    }
  };

  getRatingCertificateById = async id => {
    const res = await getRatingCertificateById(id);
    const initialValue = res.data;
    this.setState({ initialValue });
  };

  saveRatingCertificate = async ratingCertificate => {
    const { id } = this.state.initialValue;
    ratingCertificate.id = id;
    try {
      if (!id) {
        await createRatingCertificate(ratingCertificate);
      } else {
        await updateRatingCertificate(ratingCertificate);
      }
      message.success('Lưu thành công');
      this.props.history.goBack();
    } catch (err) {
      message.error('Lưu không thành công');
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.saveRatingCertificate(values);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { initialValue } = this.state;
    return (
      <Card title={initialValue.id ? 'Cập nhật XL chứng chỉ' : 'Thêm danh mục XL chứng chỉ'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormRatingGraduate {...props} />
  </Page>
));



