import { request } from 'utils';

const prefix = '/api/categories';

export const getRatingCertificates = async () =>
  request({
    prefix,
    url: `/CertificateClassification`,
    params: {},
    method: 'GET',
  });

export const getRatingCertificateById = async id =>
  request({
    prefix,
    url: `/CertificateClassification/${id}`,
    params: {},
    method: 'GET',
  });

export const createRatingCertificate = async values =>
  request({
    prefix,
    url: '/CertificateClassification',
    params: {},
    method: 'POST',
    data: { ...values },
  });

export const updateRatingCertificate = async values =>
  request({
    prefix,
    url: `/CertificateClassification/${values.id}`,
    params: {},
    method: 'PUT',
    data: { ...values },
  });

export const deleteRatingCertificate = async id =>
  request({
    prefix,
    url: `/CertificateClassification/${id}`,
    params: {},
    method: 'DELETE',
    data: {},
  });



